import React from 'react';

const PoemsPage = () => {
  const poems = [
    {
      title: "    ",
      author: "Harshit Srivastav",
      content: `     
    
    
    

   
   
   
    
     
   
     

     
     ,
    , 
    
   
    
     

     
     
     
     
    

`
    },
    {
      title: "    ",
      author: "Abhyuday Singh",
      content: `"    ,
    ,
    ,
    ,
   ,
    ,
   ,
    ,
    ,
    ,
     ,
   ,
     
    ,
    
   ,
    ,
   ,
    ,
   ,
     ,
     ,
    ,
    
,`
    }
  ];

  return (
    <div style={{
      maxWidth: '1200px',
      margin: '0 auto',
      padding: '20px',
      fontFamily: 'Arial, sans-serif',
      backgroundColor: '#f9f9f9',
      minHeight: '100vh'
    }}>
      <header style={{
        textAlign: 'center',
        marginBottom: '50px',
        borderBottom: '2px solid #333',
        paddingBottom: '20px'
      }}>
        <h1 style={{
          fontSize: '2.5em',
          color: '#333',
          textTransform: 'uppercase',
          letterSpacing: '2px'
        }}>Yadav-Manthan</h1>
      </header>

      <div style={{
        display: 'grid',
        gridTemplateColumns: 'repeat(auto-fit, minmax(300px, 1fr))',
        gap: '30px',
        padding: '20px'
      }}>
        {poems.map((poem, index) => (
          <div key={index} style={{
            backgroundColor: '#ffffff',
            borderRadius: '10px',
            padding: '25px',
            boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
            transition: 'transform 0.3s ease',
            cursor: 'pointer',
            ':hover': {
              transform: 'translateY(-5px)'
            }
          }}>
            <h2 style={{
              color: '#2c3e50',
              fontSize: '1.5em',
              marginBottom: '10px',
              borderBottom: '1px solid #eee',
              paddingBottom: '10px'
            }}>{poem.title}</h2>
            <h3 style={{
              color: '#7f8c8d',
              fontSize: '1em',
              marginBottom: '15px',
              fontStyle: 'italic'
            }}>by {poem.author}</h3>
            <p style={{
              color: '#34495e',
              lineHeight: '1.6',
              whiteSpace: 'pre-line',
              fontSize: '1.1em'
            }}>{poem.content}</p>
          </div>
        ))}
      </div>

      <footer style={{
        textAlign: 'center',
        marginTop: '50px',
        padding: '20px',
        borderTop: '1px solid #ddd',
        color: '#666'
      }}>
        <p> 2024 Yadav-Manthan Poetry Collection. All rights reserved.</p>
      </footer>
    </div>
  );
};

export default PoemsPage;